import React from "react";
import { Container, Row, Col, Card, Form, Button } from "react-bootstrap";

const ResultsTestimonialsSection = () => {
  // Exemple de taux de russite des visas par pays
  const successRates = [
    { country: "tats-Unis", successRate: "97%" },
    { country: "Canada", successRate: "94%" },
    { country: "Australie", successRate: "92%" }
  ];

  // Exemple de tmoignages
  const testimonials = [
    { name: "Marie D.", quote: "Grce  leur expertise, j'ai pu obtenir mon visa rapidement et facilement. Je recommande vivement leurs services!" },
    { name: "Thomas B.", quote: "Leur quipe dvoue m'a accompagn  chaque tape de mon voyage. Un service exceptionnel!" }
  ];

  return (
    <div className="bg-light py-5">
      <Container>
        <h2 className="text-center mb-4">Rsultats et Tmoignages</h2>
        <Row className="justify-content-center">
          <Col md={6} className="mb-4">
            <Card>
              <Card.Body>
                <h4>Taux de russite des visas par pays</h4>
                {successRates.map((result, index) => (
                  <p key={index}>{result.country}: {result.successRate}</p>
                ))}
              </Card.Body>
            </Card>
          </Col>
          <Col md={6} className="mb-4">
            <Card>
              <Card.Body>
                <h4>Tmoignages</h4>
                {testimonials.map((testimonial, index) => (
                  <div key={index} className="mb-3">
                    <h5>{testimonial.name}</h5>
                    <p>"{testimonial.quote}"</p>
                  </div>
                ))}
                <Form>
                  <Form.Group controlId="formName">
                    <Form.Label>Votre nom</Form.Label>
                    <Form.Control type="text" placeholder="Entrez votre nom" />
                  </Form.Group>
                  <Form.Group controlId="formTestimonial">
                    <Form.Label>Votre tmoignage</Form.Label>
                    <Form.Control as="textarea" rows={3} placeholder="Entrez votre tmoignage" />
                  </Form.Group>
                  <Button variant="primary" type="submit">Soumettre</Button>
                </Form>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ResultsTestimonialsSection;
