// AgencyPresentationSection.js

import React from 'react';
import image1 from "../../assets/images/buffer-1143485_1920.jpg";

function AgencyPresentationSection() {
  return (
    <section className="agency-presentation-section">
      <div className="container">
        <div className="row">
          <div className="col-md-5">
            <h2 style={{ fontSize: "45px", textAlign: "justify"}}><span style={{color: "blue"}}> Global Travel</span><br /><span style={{ color: "orange"}}>Qui sommes-nous <i className='bi bi-question-lg'></i></span></h2>
            <br />
            <br />
            <p style={{fontFamily: "Segoe UI Light", fontSize: "17px"}}>
            En tant que spcialistes de l'assistance aux voyages, nous nous engageons  rendre votre exprience de voyage aussi fluide et sans tracas que possible. De la planification initiale de votre voyage  l'arrive  votre destination, nous sommes l pour vous fournir les informations, les conseils et les services dont vous avez besoin pour vous assurer une entre en douceur dans le pays de votre choix.
            </p>
            <p style={{fontFamily: "Segoe UI Light", fontSize: "17px"}}>
            Notre gamme de services comprend une assistance pour l'obtention de visas et de permis de sjour, des conseils sur les exigences en matire de sant et de scurit, des recommandations sur les transports et l'hbergement, ainsi que des services de traduction et d'interprtation pour vous aider  communiquer efficacement dans un environnement tranger.
            </p>
            <p style={{fontFamily: "Segoe UI Light", fontSize: "17px"}}>
            Nous comprenons que chaque voyageur a des besoins uniques, c'est pourquoi nous travaillons en troite collaboration avec vous pour laborer un plan sur mesure qui rpond  vos besoins spcifiques et  vos prfrences personnelles. Notre objectif est de vous offrir une tranquillit d'esprit totale afin que vous puissiez vous concentrer sur l'essentiel : profiter pleinement de votre exprience de voyage.
            </p>
          </div>
          <div className="col-md-7">
            <br />
            <br />
            <img src={image1} alt="agence" className="img-fluid" style={{ marginLeft: "5rem", marginTop: "5rem"}}/>
          </div>
        </div>
      </div>
    </section>
  );
}

export default AgencyPresentationSection;
